import * as yup from 'yup';

const schema = yup.object().shape({
    name: yup.string().required('El campo nombre es requerido'),
    cedula: yup.string().min(7, 'Cdula invlida').max(8, 'Cdula invlida').required('El campo cdula es requerido'),
    phoneNumber: yup.string().test('is-invalid', 'El telfono debe contener 11 caracteres', async (value) => {
        return value.length === 0 || value.length === 11;
    }),
});

export default schema;
