export const COLUMNS = [
    {
        Header: 'ID',
        accessor: 'id',
    },
    {
        Header: 'Moneda',
        accessor: 'currency',
    },
    {
        Header: 'Valor en bolvares',
        accessor: 'value',
        Cell: ({ value }) => {
            return value ? value.toLocaleString() : '';
        },
    },
    {
        Header: 'Redondeo',
        accessor: 'rounding',
        Cell: ({ value }) => {
            return value ? value.toLocaleString() : 'Sin redondeo';
        },
    },
];
