import * as L from './styles';
import NumericInput from 'react-number-format';

const ClientDetails = ({ name = '', cedula = '', phoneNumber = '' }) => {
    return (
        <L.Wrapper>
            <label>Nombre:</label>
            <span>{name || 'Annimo'}</span>
            <label>Cdula:</label>
            <span>{(cedula && Number(cedula).toLocaleString()) || 'Annimo'}</span>
            <label>Telfono:</label>
            {(phoneNumber && <NumericInput displayType='text' value={phoneNumber} format='( #### ) - ### - ####' />) || (
                <span>Annimo</span>
            )}
        </L.Wrapper>
    );
};

export default ClientDetails;
